* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  font-size: 1px;
}

.section {
  padding-left: 15px;
  padding-right: 15px;
}

.section__search {
  background-color: #66a3d2;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.search-block {
  margin: 0, auto;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.search-form {
  height: 30px;
}

.gallery {
  width: 100%;
  max-width: 1140px;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 300px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  padding-bottom: 15px;
  list-style: none;
  display: grid;
}

.photo-card {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.photo-card img {
  height: 220px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info {
  justify-content: space-between;
  display: inline-flex;
}

.info-item {
  text-align: center;
}

.button-box {
  justify-content: center;
  align-items: center;
  margin: 20px auto 70px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

